'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Alert, AlertDescription } from '@/components/ui/alert';

interface DashboardStats {
  totalClients: number;
  totalEquipments: number;
  totalOrders: number;
  openOrders: number;
  lowStockItems: number;
  monthlyRevenue: number;
}

export default function DashboardPage() {
  const router = useRouter();
  const [stats, setStats] = useState<DashboardStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    fetchStats();
  }, []);

  const fetchStats = async () => {
    try {
      const response = await fetch('/api/dashboard');
      const data = await response.json();

      if (data.success) {
        setStats(data.stats);
      } else {
        setError(data.error || 'Erro ao carregar estatísticas');
      }
    } catch (error) {
      setError('Erro de conexão. Tente novamente.');
    } finally {
      setLoading(false);
    }
  };

  const navigateTo = (path: string) => {
    router.push(path);
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-96">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-gray-900 mx-auto"></div>
          <p className="mt-4 text-gray-600">Carregando estatísticas...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <Alert variant="destructive">
        <AlertDescription>{error}</AlertDescription>
      </Alert>
    );
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
        <p className="text-gray-600 mt-2">
          Visão geral do sistema de ordens de serviço
        </p>
      </div>

      {stats && (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {/* Total de Clientes */}
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">
                Total de Clientes
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats.totalClients}</div>
              <p className="text-xs text-gray-600">
                Clientes cadastrados no sistema
              </p>
            </CardContent>
          </Card>

          {/* Total de Equipamentos */}
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">
                Total de Equipamentos
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stats.totalEquipments}</div>
              <p className="text-xs text-gray-600">
                Equipamentos cadastrados
              </p>
            </CardContent>
          </Card>

          {/* Ordens de Serviço Abertas */}
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">
                OS Abertas
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-orange-600">{stats.openOrders}</div>
              <p className="text-xs text-gray-600">
                De {stats.totalOrders} ordens totais
              </p>
            </CardContent>
          </Card>

          {/* Itens com Estoque Baixo */}
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">
                Estoque Baixo
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className={`text-2xl font-bold ${stats.lowStockItems > 0 ? 'text-red-600' : 'text-green-600'}`}>
                {stats.lowStockItems}
              </div>
              <p className="text-xs text-gray-600">
                {stats.lowStockItems > 0 ? 'Itens precisam de reposição' : 'Estoque em dia'}
              </p>
            </CardContent>
          </Card>

          {/* Receita do Mês */}
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">
                Receita do Mês
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">
                R$ {stats.monthlyRevenue.toFixed(2)}
              </div>
              <p className="text-xs text-gray-600">
                Entradas do mês atual
              </p>
            </CardContent>
          </Card>

          {/* Status Geral */}
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">
                Status do Sistema
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">Online</div>
              <p className="text-xs text-gray-600">
                Sistema funcionando normalmente
              </p>
            </CardContent>
          </Card>
        </div>
      )}

      {/* Alertas */}
      {stats && stats.lowStockItems > 0 && (
        <Alert>
          <AlertDescription>
            <strong>Atenção:</strong> Existem {stats.lowStockItems} itens com estoque baixo que precisam de reposição.
          </AlertDescription>
        </Alert>
      )}

      {stats && stats.openOrders > 10 && (
        <Alert>
          <AlertDescription>
            <strong>Aviso:</strong> Você tem {stats.openOrders} ordens de serviço abertas. Considere priorizar o atendimento.
          </AlertDescription>
        </Alert>
      )}

      {/* Ações Rápidas */}
      <div className="mt-8">
        <h2 className="text-xl font-semibold text-gray-900 mb-4">Ações Rápidas</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          <Card 
            className="cursor-pointer hover:shadow-md transition-shadow"
            onClick={() => navigateTo('/orders')}
          >
            <CardContent className="p-4 text-center">
              <h3 className="font-medium text-gray-900">Nova OS</h3>
              <p className="text-sm text-gray-600 mt-1">Criar ordem de serviço</p>
            </CardContent>
          </Card>

          <Card 
            className="cursor-pointer hover:shadow-md transition-shadow"
            onClick={() => navigateTo('/clients')}
          >
            <CardContent className="p-4 text-center">
              <h3 className="font-medium text-gray-900">Novo Cliente</h3>
              <p className="text-sm text-gray-600 mt-1">Cadastrar cliente</p>
            </CardContent>
          </Card>

          <Card 
            className="cursor-pointer hover:shadow-md transition-shadow"
            onClick={() => navigateTo('/inventory')}
          >
            <CardContent className="p-4 text-center">
              <h3 className="font-medium text-gray-900">Estoque</h3>
              <p className="text-sm text-gray-600 mt-1">Gerenciar peças</p>
            </CardContent>
          </Card>

          <Card 
            className="cursor-pointer hover:shadow-md transition-shadow"
            onClick={() => navigateTo('/reports')}
          >
            <CardContent className="p-4 text-center">
              <h3 className="font-medium text-gray-900">Relatórios</h3>
              <p className="text-sm text-gray-600 mt-1">Ver relatórios</p>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}
